import React from 'react'
import Cart from './Cart'
import List from './List'
import Mine from './Mine'
import Home from './Home'
import Search1 from './Search1'
import {Detail} from './Things' 
import {BrowserRouter as Router,Switch,Route,Redirect} from 'react-router-dom'
import 'antd-mobile/dist/antd-mobile.css';

const App=()=>{
	return <Router>
		<Switch>
			<Route exact path='/complex-thirdtask/' component={Home}/>
			<Route path='/complex-thirdtask/cart' component={Cart}/>
			<Route path='/complex-thirdtask/list' component={List}/>
			<Route path='/complex-thirdtask/my' component={Mine}/>
			<Route path='/complex-thirdtask/search1' component={Search1}/>
			<Route path='/complex-thirdtask/docu/:type' component={Detail}/>
			<Route render={()=><Redirect to='/complex-thirdtask/' component={Home}/>} />
		</Switch>
	</Router>
}

export default App;